	}
}

label {
	display: block;
	margin-bottom: 5px;
}

select,
textarea,
input[type="text"],
input[type="email"],
input[type="search"],
input[type="tel"],
input[type="url"],
input[type="password"] {
	display: inline-block;
	height: auto;
	padding: 0.5rem 0.75rem;
	font-size: 16px;
