.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute;
	width: 1px;
	word-wrap: normal !important;
}

.screen-reader-text:focus {
	background-color: #eee;
	clip: auto !important;
	clip-path: none;
	color: #444;
	display: block;
	font-size: 1em;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

.alignleft {
	float: left;
	margin-right: 1.5em;
	margin-bottom: 0.25em;
}

.alignright {
	float: right;
	margin-left: 1.5rem;
}

.aligncenter {
	display: block;
	margin: 0 auto 1.5rem auto;
}

.alignnone,
.block {
	clear: both;
	margin: 0 0 1.5rem 0;
}

img.alignleft,
img.alignright {
	display: inline;
}

@media (max-width: 600px) {
	.fl-col-small {
		max-width: 100%;
	}
}

.has-white-color {
	color: white;
}

.has-white-background-color {
	background-color: white;
}

